import React, {useContext} from 'react'
import {AuthContext} from "../context/Auth/authContext";
import {Jumbotron} from "../components/Jumbotron/Jumbotron";

export const About = () => {
    const {token} = useContext(AuthContext)

    return (
        <div className="container">
            {
                token
                    ? <Jumbotron title={'Hello! You are a registered user.'}
                                 content={'To log out go to the logout page'}/>
                    : <Jumbotron title={'Hello! You are not a registered user.'}
                                 content={'To register, go to the authorization page'}/>
            }
        </div>

    )
}